.pizza-card {
  width: 300px;
  height: 400px;
  background-color: white;
  border-radius: 20px;
  margin: 20px;
}
.pizza-image {
  width: 100%;
  height: 300px;
  background-position: center;
  background-size: cover;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.pizza-name {
  margin: 5px 10px;
}

.pizza-price {
  margin: 5px 10px;
  color: tomato;
}

.pizza-order-btn {
  margin: 5px 10px;
  width: 100px;
  height: 23px;
  color: white;
  background-color: tomato;
  border: none;
  border-radius: 5px;
  transition: 0.3s all ease-in-out;
  cursor: pointer;
}

.pizza-order-btn:hover {
  transform: scale(1.1);
}

.price-order {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
