.contact {
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
}

.left-side {
  width: 50%;
  height: 90vh;
  background-position: center;
  background-size: cover;
}

.contact-right-side {
  width: 50%;
  height: 90vh;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}

.contact-title {
  font-size: 50px;
  letter-spacing: 10px;
  font-family: "Noto Serif", serif;
  font-family: "Rubik Moonrocks", cursive;
  margin: 30px 30px;
}

.form-container {
  width: 80%;
  height: 60vh;
  margin: 0px 30px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}

.form-container label {
  font-size: 20px;
  margin: 40px 0px 5px 0px;
}

.form-container input {
  border: none;
  width: 90%;
  height: 50px;
  background-color: inherit;
  border-bottom: 3px solid white;
  color: white;
  padding: 0px 5px;
  outline: none;
}

.form-container textarea {
  border: 3px solid white;
  background-color: inherit;
  color: white;
  width: 90%;
  height: 150px;
  padding: 10px 5px;
  margin: 10px 0px;
  outline: none;
}

.form-container button {
  background-color: tomato;
  border: none;
  width: 150px;
  height: 50px;
  font-size: 18px;
  color: white;
}
