.menu {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: black;
}

.menu-title {
  font-size: 50px;
  letter-spacing: 10px;
  font-family: "Noto Serif", serif;
  font-family: "Rubik Moonrocks", cursive;
  color: white;
  margin: 10px 30px;
  align-self: flex-start;
}

.menu-items {
  width: 80%;
  height: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
