.footer {
  height: 20vh;
  width: 100%;
  background-color: black;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.footer-icons svg {
  margin: 10px 10px;
  width: 50px;
  height: 50px;
  transition: 0.4s all ease-in-out;
  cursor: pointer;
}

.footer-icons svg:hover {
  transform: scale(1.1);
}

.footer-icons svg:hover:nth-child(1) {
  color: #8a3ab9;
}

.footer-icons svg:hover:nth-child(2) {
  color: #4267b2;
}

.footer-icons svg:hover:nth-child(3) {
  color: #00acee;
}

.footer-icons svg:hover:nth-child(4) {
  color: #ff0000;
}
