.navbar {
  width: 100%;
  height: 10vh;
  background-color: black;
  display: flex;
  flex-direction: row;
}

.navbar .left-side {
  flex: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 150px;
}

.navbar .left-side img {
  width: 65px;
  animation: rotate360 3s infinite linear;
}

.navbar .right-side {
  flex: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar a {
  color: white;
  text-decoration: none;
  margin: 20px;
  letter-spacing: 2px;
  cursor: pointer;
  transition: 0.4s all ease-in-out;
}

.navbar a:hover {
  transform: scale(1.1);
  color: tomato;
}

.navbar .right-side button {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
}

.navbar .right-side svg {
  font-size: 40px;
}

.navbar #open {
  padding-left: 0px;
}
.navbar #open img {
  display: none;
}

.navbar #close img {
  display: inherit;
}

.navbar #open .hidden-links {
  display: inherit;
  margin-left: 30px;
}

.navbar #close .hidden-links {
  display: none;
}

.navbar #open a {
  width: 70px;
  margin: 5px;
}
@media only screen and (max-width: 900px) {
  .navbar .right-side a {
    width: 70px;
  }
  .navbar .left-side {
    padding-left: 50px;
  }
}

@media only screen and (max-width: 600px) {
  .navbar .right-side a {
    display: none;
  }

  .navbar .right-side {
    justify-content: flex-end;
    padding-right: 50px;
  }

  .navbar .right-side button {
    display: inherit;
  }
}

@media only screen and (min-width: 600px) {
  .navbar .right-side button {
    display: none;
  }

  .hidden-links {
    display: none;
  }
}

@keyframes rotate360 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
