@import url("https://fonts.googleapis.com/css2?family=Noto+Serif&family=Rubik+Moonrocks&display=swap");

.home {
  width: 100%;
  height: 90vh;
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-container {
  width: 100%;
  height: 70%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}

.header-container h1 {
  font-size: 90px;
  color: white;
  letter-spacing: 10px;
  margin: 10px 30px;
  font-family: "Noto Serif", serif;
  font-family: "Rubik Moonrocks", cursive;
  text-align: center;
}

.header-container p {
  font-size: 20px;
  color: white;
  margin: 10px 30px;
  letter-spacing: 4px;
  text-align: center;
}

.header-container p span {
  color: tomato;
}

.header-container button {
  width: 150px;
  height: 40px;
  margin: 10px 30px;
  border: none;
  background-color: tomato;
  color: white;
  letter-spacing: 2px;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.4s all ease-in-out;
}

.header-container button:hover {
  transform: scale(1.1);
}
