.about {
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.about-top {
  width: 100%;
  height: 45vh;
  background-position: center;
  background-size: cover;
}

.about-bottom {
  width: 100%;
  height: 45vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  background-color: black;
}

.about-bottom h1 {
  font-size: 50px;
  letter-spacing: 10px;
  font-family: "Noto Serif", serif;
  font-family: "Rubik Moonrocks", cursive;
  margin: 30px 30px;
}

.about-bottom p {
  font-size: 20px;
  text-align: center;
  padding: 0px 40px;
  margin: 10px 10px;
}
